import { Component, OnInit } from '@angular/core';
import { Router             } from '@angular/router';
import { EmployeeService   } from '../employee.service';
import { Employee          } from '../employee';
import * as _ from 'lodash';

@Component({
  selector: 'app-employees',
  templateUrl: './employees.component.html',
  styleUrls: ['./employees.component.css']
})
export class EmployeesComponent implements OnInit {

  // subscriptions
  /*[1]*/ getEmployeesSub: any;
  
  // data
  employees: Employee[] = [];
  filteredEmployees: Employee[] = [];
  str: string;
  pattern: any;

  // error state
  loadingError: boolean = false;
  loading: boolean = false;

  constructor(
    private m: EmployeeService,
    private rt: Router,
    ) { }
   
  routeToEmployeeDetails(id: string) {
    this.rt.navigate(['/employee', id]);
  }
    
  onEmployeeSearchKeyUP(e: any) {
    this.str = e.target.value;
    this.filteredEmployees = _.filter(this.employees, (emp) => {
      this.pattern = new RegExp(this.str, 'i');
      return (
          this.pattern.test(emp.FirstName) || this.pattern.test(emp.LastName)  || this.pattern.test(emp.Position.PositionName)
      );        
    });
    this.loading = true;
    setTimeout(() => {this.loading = false}, 2000)
    
    
  } // end keyup  

  ngOnInit() {

    // [1.] subscribe to getEmployees()   
    this.getEmployeesSub = this.m.getEmployees().subscribe((employees) => {
        this.employees = employees;
        this.filteredEmployees = employees;
      },
      (error) => {
        if (error) {
        this.loadingError = true;
        console.log(error);
        }
      });   
  } // end of onInit

  ngOnDestroy() {
    if (this.getEmployeesSub) {
      this.getEmployeesSub.unsubscribe();
    }
  } // end of onDestroy

} // end of Component
